#tweet-quote {
  background-color: var(--color) !important;
  border-color: var(--color) !important;
  transition: background-color 1s ease;
}

#new-quote {
  background-color: var(--color) !important;
  border-color: var(--color) !important;
  transition: background-color 1s ease;
}

body {
  background-color: var(--color) !important;
  transition: background-color 1s ease;
}

#text,
#author {
  color: var(--color) !important;
  transition: color 1s ease;
}
